@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Roboto:ital,wght@0,100;0,300;0,900;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Roboto:ital,wght@0,100;0,300;0,900;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    background-color: black;
    justify-content: center;
    display: flex;

}
.navbar a{
    color: white;
    padding:24px 26px;
    text-decoration: none;
    font-size:20px;
    font-family: 'Amatic SC', cursive;
    font-weight:bold;

}

.navbar a:hover{
    background-color:#ACE0F9;
    color:black;
}
.big-title{
    text-align: center;
    font-family: 'Amatic SC',cursive;
    font-size:40px;
}
.card-container{
    display:flex;
    flex-wrap: wrap;
    justify-content:space-evenly;



}

.cards{
    padding:20px;
    background-color:black;
    border-radius:7px;
    box-shadow: 3px 3px 5px rgba(0,0,0,.7);
    margin:1rem;
    position: relative;
    overflow: hidden;

}

.title{
    font-family: 'Amatic SC',cursive;
    font-size:36px;
    padding:1rem;
    color:#fff;
}

.cards img{
    object-fit:cover;
    height:350px;
    width:500px;
    border-radius: 7px;
    -webkit-filter:grayscale(0%);
            filter:grayscale(0%)

}
.cards img:hover{
    -webkit-filter:grayscale(100%);
            filter:grayscale(100%);
}

.card-over{
    position: absolute;
    background-color:rgba(0,0,0,.5);
    color: white;
    bottom:0;
    left: 0;
    right: 0;
    padding:1rem;
    -webkit-transform:translateY(100%);
            transform:translateY(100%);
    transition: -webkit-transform 0.7s ease-in-out;
    transition: transform 0.7s ease-in-out;
    transition: transform 0.7s ease-in-out, -webkit-transform 0.7s ease-in-out;
}

.cards:hover .card-over{
    -webkit-transform:translateY(0%);
            transform:translateY(0%)

}
.App{
background: #2980B9;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #FFFFFF, #6DD5FA, #2980B9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}
